/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .edgtf-onsale,
    .edgtf-out-of-stock,
    .edgtf-new-product {
        @include edgtfWooCommerceMarksGeneralStyle();
    }
    
    .edgtf-onsale {
        @include edgtfWooCommerceSaleStyle();
        padding-right: 9px;
        padding-top: 2px;
    }
    
    .edgtf-out-of-stock {
        @include edgtfWooCommerceOutOfStockStyle();
        padding-right: 10px;
        margin-top: 5px;
    }

    .edgtf-new-product {
        @include edgtfWooCommerceNewProductStyle();
        padding-right: 10px;
        margin-top: 5px;
    }
}

.edgtf-woo-single-page {
    
    .edgtf-single-product-content {
        
        .edgtf-onsale {
            padding-right: 19px;
            margin-top: 5px;
        }
        
        .edgtf-out-of-stock,
        .edgtf-new-product {
            padding-right: 19px;
            margin-top: 5px;
        }
    }
    
    &.edgtf-woo-single-thumb-on-left-side {
        
        .edgtf-single-product-content {
            
            .edgtf-onsale {
                padding-right: 19px;
                margin-top: 5px;
            }
            
            .edgtf-out-of-stock,
            .edgtf-new-product {
                padding-right: 19px;
                margin-top: 5px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */