/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.edgtf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .edgtf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .edgtf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;
        color: #909090;
    
        .woocommerce-Price-amount{
            color: #999999;
            font-size: 13px;
            font-weight: 300;
            letter-spacing: 0.1em;
        }
    
        .edgtf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: baseline;
        }
        
        .edgtf-cart-amount{
            color: #999;
            font-size: 13px;
            font-weight: 300;
            letter-spacing: .1em;
        }

        &.edgtf-header-cart-icon-pack {
            
            .edgtf-cart-icon {
                font-size: 13px;
                line-height: 40px;
                letter-spacing: 0.2em;
                font-weight: 600;
                color: #030303;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.edgtf-header-cart-svg-path {
            
            .edgtf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .edgtf-shopping-cart-dropdown {
            max-height: 1500px;
        }
    }
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-dark-color;
    
    .edgtf-cart-icon{
        color: $header-dark-color;
        
        &:hover {
            color: $header-dark-hover-color;
        }
    }
    &:hover {
        color: $header-dark-hover-color;
    }
}

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-light-color;
    
    .edgtf-cart-icon{
        color: $header-light-color;
        
        &:hover {
            color: $first-main-color;
        }
    }
    
    &:hover {
       // color: $header-light-hover-color;
    }
    .edgtf-total-amount {
        .woocommerce-Price-amount {
            color: #fff !important;
        }
        &.hover{
            color: #fff!important;
        }
    }
}

.edgtf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 308px;
    background-color: #fff;
    box-shadow: inset 0 1px 0 $default-border-color;
    text-align: left;
    z-index: 1000;
    max-height: 0;
    overflow:hidden;
    box-sizing: border-box;
    @include edgtfTransition(all .75s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 30px;
        padding: 0;

        li {
            @include edgtfTableLayout();
            padding: 15px 0;
            box-sizing: border-box;

            &.edgtf-empty-cart {
                border-bottom: 0;
                font-size: 11px;
                font-weight: 600;
                letter-spacing: .2em;
                text-transform: uppercase;
                color: #030303;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .edgtf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 84px;
        vertical-align: top;

        img {
            display: block;
            width: 90px;
            height: 110px;
        }
    }

    .edgtf-item-info-holder {
        @include edgtfTableCellLayout();
        vertical-align: top;
        padding: 0 0 0 20px;
        top: -2px;
        

        .edgtf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0px 0;
            box-sizing: border-box;
            font-size: 11px;
        }

        .remove {
            position: absolute;
            top: -2px;
	        font-size: 22px;
	        line-height: 1;
            padding: 0 0 0 132px;

            span {
                font-size: 18px;
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .edgtf-quantity {
            display: block;
            margin: 11px 0 0 0;
            font-size: 13px;
            letter-spacing: 0.1em;
            color: $default-heading-color;
        }

        .amount {
            display: block;
            margin: 7px 0 0;
            color: $default-text-color;
            font-size: 13px;
            font-family: $default-text-font;
            font-weight: 400;
        }
        
        .woocommerce-Price-currencySymbol{
            font-size: 13px;
        }
    }

    .edgtf-cart-bottom {
        padding: 21px 0 0;

        .edgtf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: -10px 0 25px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
                font-size: 15px;
                text-transform: uppercase;
                font-weight: 600;
                letter-spacing: 0.2em;
            }

            .edgtf-total-amount {
                text-align: right;
	            font-size: 15px;
                font-weight: 700;
                letter-spacing: 0.2em;
                text-transform: uppercase;
                color: #999999!important;
                
                .woocommerce-Price-amount{
                    color: #999999;
                    font-size: 16px;
                    font-family: $default-text-font;
                    font-weight: 400;
                    letter-spacing: 0.1em;
                }
            }
        }

        .edgtf-view-cart {
            @include edgtfRelativeHolderLayout();
            height: 44px;
            line-height: 36px;
            padding: 0 0 0 3px;
            font-size: 13px;
            font-weight: 600;
	        letter-spacing: 0.1em;
            color: #fff;
            background-color: $first-main-color;
            text-align: center;
            box-sizing: border-box;
            @include edgtfTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: $default-heading-color;
            }
            
            &:after{
                content: '\35';
                font-family: 'ElegantIcons';
                font-size: 18px;
                position: relative;
                display: inline-block;
                left: 4px;
                top: 3px;
            }
        }
    }
}

.edgtf-vertical-menu-area {

    .edgtf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */