/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.edgtf-pl-holder {
    @include edgtfRelativeHolderLayout();
	
	&.edgtf-info-on-image {
		
		.edgtf-pli {
			
			.edgtf-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

    .edgtf-pli {
        padding: 0;
        z-index: 100;
		
	    .edgtf-pli-title {
		    margin: 0 0 4px;
		    
		    a{
			    //font-size: 24px;
		    }
	    }
	
	    .edgtf-pli-category {
		    margin: 6px 0 3px;
		    
		    a{
			    color: #333;
			    font-weight: 300;
			    line-height: 32px;
		    }
	    }

        .edgtf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .edgtf-pli-rating-holder {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .edgtf-pli-rating {
		    @include edgtfWooCommerceRatingsStyle();
	    }

        .edgtf-pli-price {
	        @include edgtfWooCommercePriceStyle();
        }
    }

    .edgtf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .edgtf-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .edgtf-pli-image {
            @include edgtfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .edgtf-pli-onsale,
            .edgtf-pli-out-of-stock,
            .edgtf-pli-new-product {
	            @include edgtfWooCommerceMarksGeneralStyle();
            }

            .edgtf-pli-onsale {
	            @include edgtfWooCommerceSaleStyle();
	            padding-right: 9px;
	            padding-top: 2px;
            }

            .edgtf-pli-out-of-stock {
	            @include edgtfWooCommerceOutOfStockStyle();
	            padding-right: 10px;
	            margin-top: 5px;
            }
	
	        .edgtf-pli-new-product {
		        @include edgtfWooCommerceNewProductStyle();
		        padding-right: 10px;
		        margin-top: 5px;
	        }
        }
            
        .edgtf-pli-text {
            @include edgtfAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            background-color: $default-overlay-shop-color;
            box-sizing: border-box;
            @include edgtfTransition(opacity .2s ease-out);
        }

        .edgtf-pli-text-outer {
	        @include edgtfTableLayout();
	        text-align: center;
        }

        .edgtf-pli-text-inner {
            @include edgtfTableCellLayout();

            .edgtf-pli-title,
            .edgtf-pli-category a,
            .edgtf-pli-excerpt,
            .edgtf-pli-rating,
            .edgtf-pli-price {
                color: #fff;
            }
	
	        .edgtf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .edgtf-pli-add-to-cart {

		        &.edgtf-default-skin {

			        .button,
			        .added_to_cart {
				        @include edgtfButtonSolidColor();

				        &:after {
				        	content:"";
				        	border: 1px solid $first-main-color;
				        	position: absolute;
				        	top: 4px;
				        	left: 4px;
				        	width: 100%;
				        	height: 100%;
				        	@include edgtfTransitionTransform(.25s ease-out);
            				@include edgtfTransform(translate3d(0px,0px,0));
				        }

				        &:hover {
				            &:after {
				            	@include edgtfTransform(translate3d(3px,3px,0));
				            }
				        }
			        }
		        }

		        &.edgtf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color-black;
				        }
			        }
		        }
		
		        &.edgtf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color-black;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include edgtfButtonDefaultStyle();
	            @include edgtfButtonSize();
	            white-space: nowrap;
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }

	    .edgtf-pli-link {
		    @include edgtfAbsoluteHolderLayout();
	    }
    }

    .edgtf-pli-text-wrapper {
        @include edgtfRelativeHolderLayout();
        margin: 22px 0 0;
    }
	
	&.edgtf-product-info-light {

		.edgtf-pli-inner {
			
			.edgtf-pli-text {
			    background-color: rgba(0,0,0,.8);
			}

			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: #fff;
				}
				
				.edgtf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.edgtf-product-info-dark {
		
		.edgtf-pli-inner {
			
			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: #333;
				}

				.edgtf-pli-category a {
					color: $default-heading-color;

					&:hover {
						color: rgba($default-heading-color, .8);
					}
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.edgtf-masonry-layout {
		
		.edgtf-pli-inner {
			width: 100%;
			height: 100%;
			
			.edgtf-pli-image {
				height: 100%;
			}
			
			.edgtf-pli-category{
				font-size:18px;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/

	/*************** Appear Animation - begin ***************/
	&.edgtf-enable-appear-animation {
		.edgtf-pli {
			opacity: 0;
			@include edgtfTransition(opacity 1s ease-out);

			&.edgtf-appeared {
				opacity: 1;
			}
		}
	}
	/*************** Appear Animation - end ***************/

}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */