/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.edgtf-woo-single-page {
	
	&.edgtf-woo-single-thumb-on-left-side {
		
		.edgtf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include edgtfRelativeHolderLayout();
	    float: none;
	    margin: 20px 0 -24px;
    }
        
    .edgtf-single-product-content {
        @include edgtfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    padding: 0 25px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include edgtfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .edgtf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0;
        float: left;
        box-sizing: border-box;
	
	    .edgtf-single-product-title {
		    display: block;
		    margin: -7px 0 0;
	    }
	
	    .price {
		    @include edgtfWooCommercePriceStyle();
		    font-size: 32px;
	    }
	
	    .woocommerce-product-rating {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 15px 0 18px;
		    
		    .woocommerce-review-link{
			    color: $default-text-color;
			    &:hover{
				    color: $first-main-color;
			    }
		    }
	    }
	    
	    .single_add_to_cart_button{
		    padding: 12px 41px 12px 40px;
	    }
	    
	    .woocommerce-product-details__short-description {
		    @include edgtfRelativeHolderLayout();
		    margin: 21px 0 0;
		
		    p {
			    margin: 0;
		    }
		   
	    }
	
	    .product_meta {
		    margin: 28px 0 40px;
		    font-weight: 400;
		    
		    &:before{
			    content: "Info";
			    display: block;
			    font-size: 15px;
			    font-weight: 600;
			    text-transform: uppercase;
			    letter-spacing: 0.2em;
			    color: $default-heading-color;
			    margin-bottom: 7px;
		    }
		
		    > span {
			    display: block;
			    margin: 0;
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color-black;
				    }
			    }
		    }
		
		    .sku_wrapper{
			    text-transform: capitalize;
		    }
	    }
	
	    .edgtf-woo-social-share-holder {
		    @include edgtfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
		
		    .edgtf-social-title{
			    color: #080808;
			    display: block;
			    text-transform: uppercase;
		    }
		    
		    ul{
			    li{
				    a{
					    color: $first-main-color;
						@include edgtfTransition(color .25s ease-out);

                        &:hover {
                            color: $first-main-color-black;
                        }
				    }
			    }
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: 600;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include edgtfRelativeHolderLayout();
		    margin: 38px 0 11px;
	    }
	
	    .edgtf-quantity-buttons {
		    margin: 0 20px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include edgtfRelativeHolderLayout();
		margin: 71px 0 0;
		
		ul.tabs {
			@include edgtfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-bottom: 2px solid $default-border-color;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				top: 3px;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 18px 40px;
					font-size: 13px;
					font-weight: 400;
					color: $default-text-color;
					text-transform: uppercase;
					letter-spacing: 0.15em;
				}
				
				&.active{
					border-bottom: 4px solid $first-main-color;
					a{
						color: $first-main-color;
					}
				}
			}
		}
		
		.entry-content {
			@include edgtfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 32px 0 20px;
			}
		}
		
		table {
			margin: 32px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-text-color;
				width: 1%;
				padding: 0 10px 0 0;
				font-weight: 400;
			}
			
			td {
				width: 99%;
			}
			
			&.shop_attributes{
				margin: 28px 0 20px;
			}
		}
		
		
		
		#reviews {
			@include edgtfRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 24px;
				line-height: 24px;
				margin: 31px 0 24px;
			}
			
			.woocommerce-review__author{
				font-size: 15px;
				font-weight: 600;
				text-transform: uppercase;
				letter-spacing: 0.2em;
				color: #030303;
			}
			
			.woocommerce-review__dash{
				display: none;
			}
			
			.submit{
				padding: 12px 40px 12px 41px;
			}
			
			ol.commentlist {
				list-style: none;
				margin-top: 39px;
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include edgtfRelativeHolderLayout();
					
					img {
						float: left;
						width: 100px;
						margin-top: 7px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 139px;
					
					p {
						margin: 4px 0 14px 0;
						.woocommerce-review__published-date{
							margin-left: 17px;
						}
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include edgtfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 49px 0 0;
		
		> h2 {
			display: block;
			margin: 36px 0 18px;
			text-align: left;
			font-size: 24px;
		}
		
		.product {
			
			.edgtf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
		
		.edgtf-pl-rating-holder{
			display: none;
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */