/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */

.edgtf-woocommerce-page {
	
	table.cart {
		
		thead {
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
				
				th {
					padding: 0 0 10px 0;
					text-align: left;
					font-family:$default-text-font;
					font-size: 15px;
					font-weight: 600;
					letter-spacing: 0.2em;
					color: $default-heading-color;
					text-transform: uppercase;
				}
			}
		}
		
		tbody {
			
			tr {
				border: 0;
			}
		}
		
		tr.cart_item {
			border-bottom: 1px solid $default-border-color;
			
			td {
				padding: 25px 0;
				vertical-align: middle;
				text-align: inherit;
				font-size: 15px;
				text-transform: uppercase;
				
				&.product-name {
					a{
						margin-left: 35px;
						font-size: 11px;
						font-weight: 600;
						letter-spacing: 0.2em;
						line-height: 40px;
						color: $default-heading-color;
					}
				}
				
				&.product-remove {
					padding-left: 10px;
					padding-right: 10px;
					
					a {
						color: $default-text-color;
						font-size: 0;
						line-height: 1;
						
						&:after {
							content: "\f404";
							display: inline-block;
							vertical-align: middle;
							speak: none;
							color: inherit;
							font-family: "Ionicons";
							font-size: 25px;
							line-height: inherit;
							font-style: normal;
							font-weight: normal;
							font-variant: normal;
							text-transform: none;
							text-rendering: auto;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
						}
						
						&:hover {
							color: $default-heading-color;
						}
					}
				}
				
				&.product-thumbnail {
					width: 86px;
					padding-right: 10px;
					
					a, img {
						display: block;
					}
				}
				
				.variation {
					
					> * {
						display: inline-block;
						vertical-align: middle;
						padding: 0;
						margin: 0;
					}
					
					p {
						margin: 0;
					}
				}
			}
		}
		
		td.actions {
			padding: 35px 0 36px;
			text-align: inherit;
			
			.coupon {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				float: left;
				
				label {
					display: none;
				}
				
				#coupon_code{
					padding: 15px 22px;
				}
			}
			
			> input,
			> button {
				float: right;
			}

			.button {
				margin: 0 0 0 22px;
				padding: 12px 17px 12px 19px;
			}
		}
	}
	
	.cart-collaterals {
		@include edgtfRelativeHolderLayout;
		margin: 0 0 30px;
		
		h2 {
			position: relative;
			display: block;
			margin: 0 0 26px;
			font-family:$default-text-font;
			font-size: 24px;
			font-weight: 600;
			letter-spacing: 0.2em;
			color: $default-heading-color;
			text-transform: uppercase;
		}
		
		table {
			margin: 0 0 35px;
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
			}
			
			td, th {
				padding: 14px 0;
				text-align: left;
				vertical-align: middle;
			}
			
			th {
				width: 20%;
				padding-right: 5px;
				border: 0;
			}
		}
		
		tr.shipping {
			
			#shipping_method {
				list-style: none;
				margin: 0 0 7px;
				
				li {
					margin: 3px 0;
					padding: 0;
					
					> * {
						display: inline-block;
						vertical-align: middle;
						margin: 0;
					}
				}
			}
		}
		
		.woocommerce-shipping-calculator {
			
			.shipping-calculator-button {
				display: block;
				margin: 0 0 10px;
			}
			
			p {
				margin: 0;
			}
		}
	}
	
	.cross-sells {
		margin: 30px 0 0;
		border-top: 1px solid $default-border-color;
		
		> h2 {
			display: block;
			margin: 36px 0 50px;
			text-align: center;
		}
	}
	
	.cart_totals {
		margin: 1px 0 20px;
		
		.wc-proceed-to-checkout{
			.checkout-button{
				padding: 12px 40px!important;
			}
		}
		.cart-subtotal{
			th, .amount {
				font-weight: 400;
			}
			th {
				font-family:$default-text-font;
				font-size: 15px;
				font-weight: 600;
				letter-spacing: 0.2em;
				color: $default-heading-color;
				text-transform: uppercase;
			}
		}
		.order-total{
			th, .amount {
				font-weight: 400;
			}
			
			th{
				font-family:$default-text-font;
				font-size: 15px;
				font-weight: 600;
				letter-spacing: 0.2em;
				color: $default-heading-color;
				text-transform: uppercase;
			}
		}
	}
}

.edgtf-woocommerce-page {
	
	.cart-empty {
		position: relative;
		display: block;
		font-size: 24px;
		line-height: 1.5em;
		text-align: center;
		color: $default-heading-color;
		text-transform: uppercase;
		margin: 40px 0 33px;
		letter-spacing: 0.2em;
		font-weight: 600;
	}
	
	.return-to-shop {
		position: relative;
		display: block;
		text-align: center;
		margin: 0 0 60px;
	}
}

@include phone-portrait{
	.edgtf-woocommerce-page {
		
		table.cart {
			thead{
				tr{
					th{
						font-size: 10px;
					}
				}
			}
			tr.cart_item {
				td{
				&.product-name{
					a{
						margin-left: 0px;
					}
				}
			}
		}
			td{
				&.actions{
					.button{
						margin: 0;
					}
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */