/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {

    .edgtf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .edgtf-quantity-input,
        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            text-align: center;
            box-sizing: border-box;
        }

        .edgtf-quantity-input {
            position: relative;
            height: 51px;
            width: 44px;
            padding: 0;
            margin: 0 22px 0 0;
            font-family: inherit;
            line-height: 42px;
            outline: 0;
        }

        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            position: absolute;
            right: 0;
            width: 22px;
            font-weight: 600;
            height: 25px;
            line-height: 20px;
            border-left: 0;
            cursor: pointer;
            @include edgtfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color-black;
            }
        }

        .edgtf-quantity-plus {
            top: 0;
            border-bottom: 0;
            
            &:before{
                content:"\f106";
                font-family: "Font Awesome 5 Free";
                display: inline-block;
            }
        }

        .edgtf-quantity-minus {
            bottom: 0;
    
            &:before{
                content:"\f107";
                font-family: "Font Awesome 5 Free";
                display: inline-block;
            }
        }
    }
}

.woocommerce-page .edgtf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
        padding: 12px 18px;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */