/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.edgtf-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include edgtfButtonDefaultStyle();
        @include edgtfButtonSize();
        @include edgtfButtonSolidColor();
        cursor: pointer;
        z-index: 3;
        padding: 12px 40px;
        &:hover {
            @include edgtfButtonSolidHoverColor();
        }
    }
    
    .edgtf-search-submit{
        height: 45px;
        i{
            font-size: 18px;
        }
    }
    
    a.button.added {
        display: none;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */