/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 90px;
    $inner-padding: 25px; // distance between image and content
    $widget-list-item-padding: 15px; // same as sidebar vars
    margin-bottom: 24px!important;
    clear: both;

    li {
        position: relative;
    }
    
    &.widget_product_categories{
        ul{
            li{
                a{
                    &:hover{
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include edgtfH5();
    }
    
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 12px;
            margin-top: 12px;
            letter-spacing: 0;

            span {
                max-width: 100px;
                letter-spacing: 0;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color-black;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 22px;
        }

        .price_slider_wrapper {
            
            .price_slider{
                width: calc(100% - 22px);
            }
            
            .ui-widget-content {
                background-color: $default-border-color;
                height: 2px;
                position: relative;

                .ui-slider-handle {
                    background-color: $first-main-color;
                    cursor: pointer;
                    height: 6px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -2px;
                    width: 22px;
                    z-index: 2;
                    
                }

                .ui-slider-range {
                    background-color: $first-main-color;
                    display: block;
                    height: 2px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 17px 0 0;

            .button {
                float: right;
                height: 33px;
                padding: 0 20px;

                @include edgtfButtonSolidColor();

                &:hover {
                    @include edgtfButtonSolidHoverColor();
                }
            }

            .price_label {
                float: left;
                line-height: 33px; // to match button height
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: $thumb;
                position: relative;
                margin-bottom: 34px;

                a {
                    padding: 10px 0 0;
                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        height: 110px;
                        top: $widget-list-item-padding;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 12px 0 0 #{$thumb + $inner-padding};
                        position: relative;
                        
                        &.product-title{
                            font-size: 11px;
                            font-weight: 600;
                            letter-spacing: 0.2em;
                            color: #000;
                            text-transform: uppercase;
                            top: -2px;
                            
                            &:hover{
                                color: $first-main-color;
                            }
                        }
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
            }
        }
        
        .amount{
            font-size: 13px;
        }
    }
    
    &.widget_recently_viewed_products{
        ul {
            margin-top: -15px;
            margin-bottom: 39px;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart,
    &.widget_recent_reviews {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include edgtfH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include edgtfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include edgtfButtonDefaultStyle();
                @include edgtfButtonSize();
                @include edgtfButtonSolidColor();

                &:hover {
                    @include edgtfButtonSolidHoverColor();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
    .tagcloud, .product_list_widget{
        margin-top: -6px;
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */